import { Component } from "react";


class App extends Component {
   state = {
      date: '11.11.2018',
      arr: ['t', 's', 'a']
   }

   render() {
      return (
         <div className='body'>
            <div className="bands">
               <div className="band"></div>
               <div className="band"></div>
               <div className="band"></div>
               <div className="band"></div>
            </div>
            <div className="vertical-date">{this.state.date}</div>
              
            <div className="logo"></div>
            <div className="center">
               <div className="text">Comming Soon</div>
               <input type="text" name="email" id="email" />
               <div className="input-text">Enter your email and well holla at you when making is made.</div>
            </div>


            <style jsx>{`
            .body{
               width: 100%;
               height: 95vh;
               display:flex;
               justify-content: center;
               align-items: center;
            }

            .center {
               text-align: center;
            }

            .text {
               font-size: 80px;
            }

            input[type=text] {
               width: 80%;
               margin: 0 auto;
               padding: 10px;
            }
            

         `}</style>
         </div>
      );
   }
}

export default App;